import React, { useState, useEffect } from "react";
import { OpenAIClient, AzureKeyCredential } from "@azure/openai";
import Loader from "./Loader";

const OpenAIChat = () => {
  const endpoint = "https://r-openai.openai.azure.com/";
  const azureApiKey = "77bed86b013e48989337687648b0b987";
  const [messages, setMessages] = useState([
    { role: "system", content: "Hello! How can I assist you today?" },
  ]);
  const [loading, setLoading] = useState(false);

  const fetchChatCompletions = async (userMessage) => {
    setLoading(true);
    const client = new OpenAIClient(
      endpoint,
      new AzureKeyCredential(azureApiKey)
    );
    const deploymentId = "openai-gpt35t0301";
    const params = {
      temperature: 0.1,
      max_tokens: 150,
      top_p: 0.6,
      frequency_penalty: 0,
      presence_penalty: 0,
      stop: [
        "seek medical",
        "However if you have",
        "if the pain persists",
        "if your symptoms",
      ],
    };
      const result = await client.getChatCompletions(
        deploymentId,
        messages.concat(userMessage),
        params
      );
      const responseMessage = result.choices[result.choices.length - 1]?.message;
    if (responseMessage) {
      setLoading(false);
      setMessages((prevMessages) => [...prevMessages, responseMessage]);
    }
  };

  const sendMessage = async (text) => {
    const userMessage = { role: "user", content: text };
    setMessages((prevMessages) => [...prevMessages, userMessage]);
    await fetchChatCompletions(userMessage);
  };

  const handleInputKeyDown = (event) => {
    if (event.key === "Enter") {
      const userInput = event.target.value;
      if (userInput.trim() !== "") {
        sendMessage(userInput);
        event.target.value = "";
      }
    }
  };

  return (
    <div className="chatbot">
      <h1>
        OpenAI Chat Completions Sample using this deploymentId openai-gpt35t0301
      </h1>
      <div>
        {messages.map((message, index) => (
          <div
            key={index}
            className={message.role === "user" ? "prompt" : "response"}
          >
            {message.role === "user" ? (
              <b>Me:&nbsp;</b>
            ) : (
              <b>Assistant:&nbsp;</b>
            )}
            {message.content}
          </div>
        ))}
      </div>
      <div className={loading ? "q-processing" : "q-completed"}>
        <div>{loading && <Loader />}</div>
        <input type="text" onKeyDown={handleInputKeyDown} disabled={loading} />
        
      </div>
    </div>
  );
};

export default OpenAIChat;
